import { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Spinner } from "react-bootstrap";
import axios from "axios";
// import ReCAPTCHA from "react-google-recaptcha";
import { useTranslation } from "react-i18next";
import Layout from "../layout/layout";
import "./contact.css";
import MessageModal from "./messageModal";
import { serverPath } from "../../config/key";

const ContactUs = () => {
  const { t } = useTranslation();
  // inital state
  const contactInit = {
    firstName: "",
    lastName: "",
    phone: "",
    email: "",
    message: "",
  };

  // States
  const [fieldState, setFieldState] = useState({ ...contactInit });
  const [loader, setLoader] = useState(false);
  const [errors, setErrors] = useState(fieldState);
  const [show, setShow] = useState(false);
  const [infoMsg, setInfoMsg] = useState("");

  // utils function
  const handleShow = () => setShow(!show);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFieldState({ ...fieldState, [name]: value });
    setErrors({ ...errors, [name]: "" });
  };

  const validateContactField = () => {
    let valid = true;
    let error = {};
    if (fieldState.firstName.trim() === "") {
      error.firstName = t("info.firstName");
      valid = false;
    }
    if (fieldState.lastName.trim() === "") {
      error.lastName = t("info.lastName");
      valid = false;
    }
    if (fieldState.phone.trim() === "") {
      error.phone = t("info.phone");
      valid = false;
    }
    if (fieldState.email.trim() === "") {
      error.email = t("info.email");
      valid = false;
    }
    return { valid, error };
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoader(true);
    setErrors({});
    const contactValidation = validateContactField();
    if (!contactValidation.valid) {
      setErrors({ ...contactValidation.error });
      setLoader(false);
      return;
    }
    try {
      const response = await axios.post(`${serverPath}/public/contact`, {
        ...fieldState,
      });
      const {
        data: { code },
      } = response || {};
      setFieldState({ ...contactInit });
      if (code === 200) {
        handleShow();
        setInfoMsg(t("info.success"));
        setLoader(false);
      }
    } catch (error) {
      setLoader(false);
      setInfoMsg(t("info.error"));
      handleShow();
    }
  };
  return (
    <Layout>
      <div className="contact-page">
        <div className="container">
          <div className="contact-title">
            <h2>{t("contact.heading")}</h2>
            <p>{t("contact.paragraph1")}</p>
            <p>{t("contact.paragraph2")}</p>
          </div>
          <div className="contact-form">
            <Form onSubmit={handleSubmit}>
              <div className="form-grid">
                <Form.Group className="form-group">
                  <Form.Label>{t("contact.fName")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder={t("contact.fNamePlaceholder")}
                    name="firstName"
                    onChange={handleChange}
                  />
                  {!!errors?.firstName && (
                    <p className="text-danger">{errors?.firstName}</p>
                  )}
                </Form.Group>
                <Form.Group className="form-group">
                  <Form.Label>{t("contact.lName")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder={t("contact.lNamePlaceholder")}
                    name="lastName"
                    onChange={handleChange}
                  />
                  {!!errors?.lastName && (
                    <p className="text-danger">{errors?.lastName}</p>
                  )}
                </Form.Group>
              </div>
              <Form.Group className="form-group">
                <Form.Label>{t("contact.email")}</Form.Label>
                <Form.Control
                  type="email"
                  placeholder={t("contact.emailPlaceholder")}
                  name="email"
                  onChange={handleChange}
                />
                {!!errors?.email && (
                  <p className="text-danger">{errors?.email}</p>
                )}
              </Form.Group>
              <Form.Group className="form-group">
                <Form.Label>{t("contact.phone")}</Form.Label>
                <Form.Control
                  type="tel"
                  placeholder={t("contact.phonePlaceholder")}
                  name="phone"
                  onChange={handleChange}
                />
                {!!errors?.phone && (
                  <p className="text-danger">{errors?.phone}</p>
                )}
              </Form.Group>
              <Form.Group className="form-group">
                <Form.Label>{t("contact.message")}</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={4}
                  placeholder={t("contact.msgPlaceholder")}
                  name="message"
                  onChange={handleChange}
                />
              </Form.Group>
              {/* <ReCAPTCHA sitekey="Your client site key" hl={"en"} /> */}
              <Button className="primary-btn" type="submit" disabled={loader}>
                {loader ? <Spinner animation="border" /> : t("contact.button")}
              </Button>
            </Form>
          </div>
        </div>
      </div>
      <MessageModal show={show} handleShow={handleShow} infoMsg={infoMsg} />
    </Layout>
  );
};

export default ContactUs;
