import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";

const MessageModal = ({ show, handleShow, infoMsg }) => {
  return (
    <Modal show={show} onHide={handleShow} centered className="info-modal">
      <Modal.Header closeButton></Modal.Header>
      <Modal.Body>
        <h6>{infoMsg}</h6>
      </Modal.Body>
      <Modal.Footer>
        <Button className="secondary-btn" onClick={handleShow}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default MessageModal;
