import { useTranslation } from "react-i18next";
import axios from "axios";
import icon1 from "../../assets/images/icons/step1_icon.png";
import { serverPath } from "../../config/key";
import { useEffect, useState } from "react";
import Storage from "../../utils/storage";

const ConsultingArea = () => {
  const { t } = useTranslation();
  const [allConsultation, setAllConsultation] = useState([]);
  const getAllConsultation = async () => {
    try {
      const response = await axios.get(
        `${serverPath}/consultation/getAllConsultation`
      );
      const { status, data: { result = [] } = {} } = response || {};
      if (status === 200) {
        setAllConsultation(result);
      }
    } catch (error) {
      if (axios.isAxiosError(error) && error.response) {
        console.log("error in Consultation api ==>", error);
      }
    }
  };
  useEffect(() => {
    getAllConsultation();
  }, []);

  return (
    <div className="consulting-area" id="consulting-section">
      <div className="container">
        <div className="consulting-area-title">
          <h2>{t("consulting.heading")}</h2>
        </div>
        <div className="consulting-area-grid">
          {allConsultation.map((el, i) => {
            return (
              <div className="consulting-area-box" key={i}>
                <img src={icon1} alt="icon" />
                <p>{el[Storage.get("lang").apiShortCode]}</p>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default ConsultingArea;
